/*importing font from google fonts*/
@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300;400;500&display=swap');
body{
  background-image: url("http://localhost:3000/rustoga-split-square_MediumSize.jpeg");
  background-size: cover;
  background-position: center;  
  width: 100vw; height: 100vh;
} 





.iFrame {

  top: 0;
  left:0;
  justify-content: center;
  scale: 100 px;
  margin: auto;
  display: block;
  width: 100vw; height: 100vh;
  display: flex;


} 
 
.iFrameContainer {


}

h1 {
  font-size: 2.75em;
  font-family: 'Source Code Pro', monospace;
  line-height: 1.25;
  font-weight: 400;
  color: whitesmoke;
}

p {

  color: #FFFFFF;
  margin-top: -24px;
  font-family: 'Source Code Pro', monospace;
  font-size: 1em;
  line-height: 1.75;
  font-weight: 400;
  align-content: flex-start 
  

}


.socialContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
background-color: none;



}



/*menu container*/
.NavContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
background-color: none;
/*background-color: whitesmoke;*/
  padding-top: 5vh;
  padding-bottom: 5vh;
}

  /*menu text formatting*/
a, a:focus, a:active {
  text-decoration: none;
  color: whitesmoke;
}

.navItems{
  display: flex;
  width: auto;
  height: 1.5rem;
  line-height: 1.5rem;
  vertical-align: middle;
  padding: 0 0.75rem;
  font-size: 1em;
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  border-radius: 0rem;
  direction: var(--site-language-direction);
  transition: color 0.25s ease, background-color 0.25s ease, border-color 0.25s ease;
}

.containerVertical{
  
display: flex;
justify-content: center;

}

.container{
max-width: 30vw;
position: flex;
flex-direction: column;
margin-right: 1vw;
}
.textContainer {
  max-width: 50vw;
  padding-left: 25vw;
  text-align: left;
}


img{
display: block;
max-width: 100%;

}

.imglink{
width: 25vw;


}
  

.video{
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}